@charset "UTF-8";
li,
ul {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-size: normal; }

*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/montserrat-thin-webfont.woff2") format("woff2"), url("../fonts/Montserrat/montserrat-thin-webfont.woff") format("woff");
  font-weight: 100; }

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/montserrat-light-webfont.woff2") format("woff2"), url("../fonts/Montserrat/montserrat-light-webfont.woff") format("woff");
  font-weight: 300; }

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/montserrat-regular-webfont.woff2") format("woff2"), url("../fonts/Montserrat/montserrat-regular-webfont.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/Montserrat/montserrat-bold-webfont.woff") format("woff");
  font-weight: 700; }

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/montserrat-black-webfont.woff2") format("woff2"), url("../fonts/Montserrat/montserrat-black-webfont.woff") format("woff");
  font-weight: 900; }

body {
  font-family: 'Montserrat';
  font-size: 16px;
  position: relative; }

.container {
  width: 1140px; }
  .container_header {
    display: flex;
    justify-content: space-between; }
  .container_menu {
    display: flex;
    justify-content: center; }

.cart {
  position: relative; }
  .cart__icon {
    height: 20px;
    width: 18px;
    z-index: -10; }
  .cart__num {
    font-size: 11px;
    color: #fff;
    position: absolute;
    top: -30%;
    left: 75%;
    z-index: 20; }
    .cart__num:after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 20px;
      height: 20px;
      background-color: #3151cc;
      border-radius: 50%;
      z-index: -5; }

.city {
  display: flex;
  font-size: 11px;
  align-items: center; }
  .city__icon {
    height: 20px;
    width: 16px;
    margin-right: 15px; }

.header {
  display: flex;
  justify-content: center;
  padding: 22px 0;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3); }

.icons {
  display: flex; }
  .icons__item:not(:last-of-type) {
    margin-right: 40px; }
  .icons__item:last-of-type {
    margin-right: 10px; }
  .icons__link {
    display: block;
    width: 100%;
    height: 100%; }
  .icons__search {
    height: 20px;
    width: 20px; }
  .icons__avatar {
    height: 20px;
    width: 17px; }

.logo {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2.85px;
  position: relative; }
  .logo:after {
    content: '®';
    position: absolute;
    top: 0;
    left: 100%;
    font-size: 11px; }

.menu {
  display: flex;
  justify-content: center;
  font-size: 11px;
  text-transform: uppercase;
  padding: 30px 0; }
  .menu__item {
    transition: color .5s; }
    .menu__item.active, .menu__item:hover {
      color: #949494; }
    .menu__item:not(:last-of-type) {
      margin-right: 40px; }
